.navbar{
    background: rgb(30, 30, 30);
    height: 5rem;
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
}
.logo{
    object-fit: cover;
    height: 3.5rem;
    width: 4rem;
}
.desktopMenuListItem{
   margin: 1rem;
   cursor: pointer;
}
.desktopMenuListItem:hover{
  color: rgb(0, 157, 255);
  padding-bottom: 0.5rem;
  border-bottom: 3px solid rgb(0, 157, 255);
}
.active{
  color: rgb(0, 157, 255);
  padding-bottom: 0.5rem;
  border-bottom: 3px solid rgb(0, 157, 255);
}
.desktopMenuImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
}
.desktopMenuBtn{
    background: white;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem 1rem;
    border-radius: 2rem;
    cursor: pointer;
    transition: background-color 0.5s ease;
}
.desktopMenuBtn:hover{
    background-color: rgb(0, 157, 255);
    color: white;
 }
.active{
    color: rgb(0, 157, 255);
    padding-bottom: 0.5rem;
    border-bottom: 3px solid rgb(0, 157, 255);
}
.mobMenu{
   display: none;
   object-fit: cover;
   height: 1.8rem;
  
}
.navMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40, 40, 40);
    border-radius: 1rem;
}
.listItem{
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgba(30, 30, 30);
}
.animated-element6 {
    opacity: 0;
    transform: translateY(-50px);
    transition: opacity 0.5s, transform 1s;
  }
  .settled6 {
    opacity: 1;
    transform: translateY(0);
  }
@media screen and (max-width:720px){
    .mobMenu{
        display: flex;
        color: rgb(0, 157, 255);
        font-size: 20px;
    }
    .desktopMenu{
        display: none;
    }
    .desktopMenuBtn{
        display: none;
    }
}