.contactPage{
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}
.contactPageTitle {
    font-size: 3rem;
    margin-bottom: 1rem;
}
.clientDesc{
    font-weight: 300;
    font-size: 1rem;
}
.clientImgs{
    margin: 5rem 0;
    display: flex;
    flex-wrap: wrap;
}
.clientImg{
    object-fit: cover;
    width: 30%;
    min-width: 9rem;
    margin: auto;
    padding: 0.25rem ;
}
.github{
    padding: 2rem;
    margin-top: 20px;
}
.contactDesc{
    padding: 1rem;
    font-size: medium;
    font-weight: 300;
}
.contactFrom{
   margin: 1rem;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   width: 90vw;
   max-width: 60rem;
}
.name, .email, .msg{
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color: white;
    border: none;
    border-radius: 0.5rem;
    background: rgb(40, 40, 40);
}
.submitBtn{
    background: white;
    border: none;
    border-radius: 0.5rem;
    margin: 2rem;
    padding: 0.75rem 3.5rem;
    transition: background-color 0.5s ease;
}
.submitBtn:hover{
    background-color:rgb(0, 157, 255);
    color: white;
}
.links{
    display: flex;
    flex-wrap: wrap;
}
.link{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.75rem;
}
.github-calendar-container {
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    margin-bottom: 50px;
  }
    .animated-element3 {
    opacity: 0;
    transform: translateY(50px);
    transition: opacity 0.5s, transform 0.7s;
  }

  .settled3 {
    opacity: 1;
    transform: translateY(0);
  }
  .animated-element4,.animated-element5 {
    opacity: 0;
    transform: translateY(50px);
    transition: opacity 0.5s, transform 0.7s;
  }
  .settled4,.settled5 {
    opacity: 1;
    transform: translateY(0);
  }