#intro{
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    top: 0;
}
.bg{
    position: absolute;
    top: 5rem;
    right: 15vw;
    z-index: -1;
    object-fit: contain;
    height: 50vh;
    border-radius: 30px;
}
.introcontent{
   height: 100vh;
   width: 100vw;
   padding: 2rem;
   font-size: 3rem;
   font-weight: 700;
   display: flex;
   flex-direction: column;
   justify-content: center;
}
.hello{
    font-size: 1.75rem;
    font-weight: 100;
}
.introName{
    color: rgb(0, 157, 255);
}
.introPara{
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
}
.btn{
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 2rem;
    border: none;
    border-radius: 2rem;
    cursor: pointer;
    transition: background-color 0.5s ease;
}
.btn:hover{
    background-color: rgb(0, 157, 255);
    color: white;
 }
.animated-element {
    opacity: 0;
    transform: translateY(50px);
    transition: opacity 0.5s, transform 0.7s;
  }
  .settled {
    opacity: 1;
    transform: translateY(0);
  }
@media screen and (max-width:840px){
    /* .bg{
        right: -10vw;
    }
    .introcontent{
        font-size: 10vw;
    }
    .hello{
        font-size: 4.5vw;
    } */
    #intro{
        height: calc(90vh);
    }
    .bg{
        /* display: none; */
        right: 10vw;
        top: 0;
    }
    .introcontent{
        font-size: 10vw;
    }
    .hello{
        font-size: 4.5vw;
    }
}
@media screen and (max-width:540px){
    .introName{
        color: rgb(40,40,40);
    }
    #intro{
        height: calc(70vh);
    }
    .hello{
        width: fit-content;
        background-color: white;
        color: black;
    }
}