#projects{
    min-height: calc(100vh - 4rem);
    margin: 0 auto;
    width: 100vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
}
.projectsTitle{
  margin: 1rem 0;
  font-size: 3rem;
}
.projectsDesc{
    font-weight: 300;
    font-size: 1rem;
    max-width: 45rem;
}
/* .projectsImgs{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
} */
.projectImage{
   width: 100%;
}
.projectsBtn{
    margin: 3rem 0;
    padding: 0.5rem 2.5rem;
    border: none;
    border-radius: 2rem;
    background: white;
    font-size: 1rem;
    transition: background-color 0.5s ease;
}
.projectsBtn:hover{
    background-color: rgb(0, 157, 255);
    color: white;
}
.skillBars{
    margin: 1.5rem auto;
    width: 100vw;
    max-width: 80%;
    text-align: left;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 16px;
    padding: 16px;
  }
  .skillBar{
      display: flex;
      margin: 1rem;
      padding: 1.5rem;
      padding-left: 0.5rem;
      border-radius: 0.5rem;
      background: rgb(50, 50, 50);
  }
  .animated-element2 {
    opacity: 0;
    transform: translateX(-50px);
    transition: opacity 0.5s, transform 0.7s;
  }

  .settled2 {
    opacity: 1;
    transform: translateY(0);
  }
