#skills{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}
.skillTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}
.skillDesc{
    font-weight: 200;
    font-size: 0.5rem;
    max-width: 50rem;
    padding: 0 2rem;
}
.skillBars{
  margin: 1.5rem auto;
  width: 100vw;
  max-width: 80%;
  text-align: left;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 16px;
  padding: 16px;
}
.skillBar{
    display: flex;
    margin: 1rem;
    padding: 1.5rem;
    padding-left: 0.5rem;
    border-radius: 0.5rem;
    background: rgb(50, 50, 50);
}
.skillBarImg{
   object-fit: cover;
   height: 3rem;
   width: 3rem;
   margin-right: 2rem;
}
.skillBarText>p{
    font-size: 0.9rem;
    font-weight: 0.9rem;
}
.animated-element1 {
    opacity: 0;
    transform: translateX(100px);
    transition: opacity 0.5s, transform 0.7s;
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
  }

  .settled1 {
    opacity: 1;
    transform: translateY(0);
  }
@media screen and (max-width:720px) {
    .skillBarText>p {
        font-size: 2vw;
    } 
}
@media screen and (max-width:480px) {
    .skillBarText>p {
        font-size: 3vw;
    } 
    .skillBarImg{
        height: 2.25rem;
        width: 2.25rem;
    }
    .skillBarText>h2{
        font-size: 5vw;
    }
}